}



.front::before, .back::before {
    content: '';
    position: absolute;
    top: 3%;
    bottom: 3%;
    left: 3%;
    right: 3%;
    border-radius: 15%;
    -webkit-transform: translateZ(20px);
            transform: translateZ(20px); border: 1px solid #00FFB5;
    background-color: transparent;
 

   
  }





